/* RestaurantPage.css */
.restaurant-page {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: calc(100vh - 40px);
    padding: 20px;
    background: linear-gradient(135deg, #2c3e50, #34495e);
    color: white;
  }
  
  .restaurant-card {
    background-color: #1c2833;
    padding: 40px;
    border-radius: 16px;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
    text-align: center;
    max-width: 600px;
    width: 100%;
  }
  
  .restaurant-name {
    font-size: 2.5rem;
    margin-bottom: 20px;
    color: #4db8ff;
  }
  
  .restaurant-description {
    font-size: 1.25rem;
    margin-bottom: 10px;
  }
  
  .restaurant-note {
    font-size: 1rem;
    color: #aaa;
  }
  