.staff-login-container {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(135deg, #ffffff 0%, #f5f5f5 100%);
    padding: 1rem;
  }
  
  .staff-login-card {
    background-color: #FFD700;
    padding: 2.5rem;
    border-radius: 25px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 400px;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    position: relative;
    overflow: hidden;
  }
  
  .login-title {
    text-align: center;
    font-size: 2.5rem;
    font-weight: bold;
    color: #000;
    margin-bottom: 1rem;
    font-family: 'Bahnschrift ', cursive;
  }
  
  .login-form {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
  }
  
  .input-group {
    width: 100%;
  }
  
  .login-input {
    width: 100%;
    padding: 1rem 1.5rem;
    font-size: 1.1rem;
    border: 2px solid #000;
    border-radius: 50px;
    background-color: white;
    outline: none;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
    font-family: 'Bahnschrift ', cursive;
  }
  
  .login-input::placeholder {
    color: #666;
    font-family: 'Bahnschrift ', cursive;
  }
  
  .login-input:focus {
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .login-button {
    background-color: white;
    border: 2px solid #000;
    border-radius: 25px;
    padding: 1rem 2rem;
    font-size: 1.2rem;
    font-weight: 600;
    color: #000;
    cursor: pointer;
    transition: transform 0.2s ease, background-color 0.2s ease;
    align-self: center;
    margin-top: 1rem;
    font-family: 'Bahnschrift ', cursive;
    min-width: 120px;
  }
  
  .login-button:hover {
    transform: translateY(-2px);
    background-color: #000;
    color: white;
  }
  
  .fries-icon {
    position: absolute;
    bottom: 1rem;
    right: 1rem;
    font-size: 2rem;
    transform: rotate(-15deg);
  }
  
  .error-message {
    color: #ff4444;
    font-size: 0.9rem;
    margin: 10px 0;
    text-align: center;
  }
  
  .login-button:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
  }
  
  @media (max-width: 480px) {
    .staff-login-card {
      padding: 2rem 1.5rem;
    }
  
    .login-title {
      font-size: 2rem;
    }
  
    .login-input {
      padding: 0.8rem 1.2rem;
      font-size: 1rem;
    }
  
    .login-button {
      padding: 0.8rem 1.5rem;
      font-size: 1rem;
    }
  } 